/* _banner.scss */

.banner {
    margin-top: 5px;
    position: relative;
    width: 100%;
    height: 223px;
    border-radius: 25px;
    overflow: hidden;
    margin-bottom: 50px;
  }
  
  .banner-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(70%);
  }
  
  .banner-title {
    position: absolute;
    top: 33%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 48px;
    font-weight: 500;
    text-align: center;
  }
  
  @media (max-width: 768px) {
    .banner {
    height: 111px;
    margin-bottom: 22px;
    }

    .banner-title {
    font-size: 24px;
    text-align: left;
    transform: none;
    top: 0;
    left: 0;
    width: 217px;
    margin-left: 10px;
    }
  }